'use client';

import { Icon } from '@iconify/react';
import { useTheme } from 'next-themes';
import { AnimatePresence, motion } from 'framer-motion';
import SunIcon from '../icons/SunIcon.svg'
export default function ThemeSwitcher() {
    const { setTheme, resolvedTheme } = useTheme();

    const isDark = resolvedTheme === 'dark';

    const handleClick = () => {
        setTheme(isDark ? 'light' : 'dark');
    };

    return (
        <button
            type="button"
            aria-label={isDark ? 'Switch to light mode' : 'Switch to dark mode'}
            onClick={handleClick}
            className="h-8 w-8 flex items-center justify-center rounded-full p-2 transition-colors duration-300 hover:bg-neutral-200 dark:hover:bg-neutral-800"
        >
            <AnimatePresence mode="wait" initial={false}>
                <motion.span
                    key={resolvedTheme || 'unknown'}
                    initial={{ opacity: 0, rotate: -90, scale: 0.7 }}
                    animate={{ opacity: 1, rotate: 0, scale: 1 }}
                    exit={{ opacity: 0, rotate: 90, scale: 0.7 }}
                    transition={{ duration: 0.3 }}
                >
                    <Icon
                        className="h-6 w-6"
                        icon={
                            resolvedTheme === 'dark'
                                ?  <SunIcon className="h-6 w-6 transition duration-300" />
                                : resolvedTheme === 'light'
                                    ? 'lets-icons:moon'
                                    : 'lets-icons:moon' // <-- fallback на SSR
                        }
                    />
                </motion.span>
            </AnimatePresence>
        </button>
    );
}
